<script>
  import { elasticOut } from "svelte/easing";
  import { fade, fly } from "svelte/transition";
  import ModalResponseWithCharacter from "../components/modals/ModalResponseWithCharacter.svelte";
  import ModalWin from "../components/modals/ModalWin.svelte";
  import ModalWithCharacter from "../components/modals/ModalWithCharacter.svelte";
  import { score } from "../game";
  import { missions } from "../missions";

  let startedMissions = [];
  let availableMissions = [];
  let winModal;
  let winText;
  let winCharacter;

  let selectMission;
  let selectMissionLevel;
  let message;
  let modalToggle;
  let character;

  let responseModalToggle;

  /*  $: startedMissions = $missions.filter((m) => m.started && !m.completed);
  $: availableMissions = $missions.filter((m) => !m.started);
  $: completedMissions = $missions.filter((m) => m.completed); */

  $: if ($missions) {
    startedMissions = $missions.filter((m) => m.started && !m.completed);
    availableMissions = $missions.filter(
      (m) => !m.started && !startedMissions.length
    );
  }

  const startThemission = (id) => {
    $missions.find((m) => m.id == id).started = 1;
    selectMission = $missions.find((m) => m.id == id);
    message = selectMission.levels.find((l) => !l.completed).text;
    character = selectMission.levels.find((l) => !l.completed).lvl == 0 ? 1 : 0;
    modalToggle = true;
    $missions = [...$missions];
  };

  const followUpMission = (id, char = 1) => {
    selectMission = $missions.find((m) => m.id == id);
    selectMissionLevel = selectMission.levels.find((l) => !l.completed);
    character = selectMissionLevel.lvl == 0 ? 1 : char;
    message = selectMission.levels.find((l) => !l.completed).text;
    responseModalToggle = true;
  };

  const closeResponseModal = (e) => {
    const answer = e.detail.response;
    let success = answer == selectMissionLevel.objective;
    if (success) {
      selectMissionLevel.completed = 1;
      $score += selectMissionLevel.points;
      startedMissions = [...startedMissions];
      if (selectMissionLevel.lvl == selectMission.levels.length - 1) {
        winText = "BRAVOO!";
        winCharacter = 1;
        winModal = true;
      } else if (selectMissionLevel.lvl < selectMission.levels.length - 1) {
        setTimeout(() => {
          followUpMission(selectMission.id, 0);
        }, 1500);
      }
    } else {
      winText = "Ouups... Raté!";
      winCharacter = 2;
      winModal = true;
      setTimeout(() => {
        winModal = false;
      }, 3000);
    }
    responseModalToggle = false;
  };
  const justCloseResponseModal = () => {
    responseModalToggle = false;
  };
  const closeModal = () => {
    modalToggle = false;
  };

  function spin(node, { duration, delay }) {
    return {
      duration,
      delay,
      css: (t) => {
        const eased = elasticOut(t);

        return `
					transform: scale(${eased}) rotate(${eased * 1080}deg);
          `;
      },
    };
  }
</script>

<div class="is-flex-center is-100-height ">
  {#if startedMissions.length}
    <div>
      <span class="tag is-warning is-size-5">ACTIVE MISSIONS</span>
    </div>
    {#each startedMissions as mission, i}
      <div
        class="card border-{mission.difficulty == 3
          ? 'danger'
          : mission.difficulty == 2
          ? 'warning'
          : 'success'}"
        in:fade={{ delay: 1000 }}
        out:fade
        on:click={() => followUpMission(mission.id)}
      >
        <p class="is-inline-block is-size-4">
          {mission.title}
        </p>
        <div class="is-inline-block">
          {#each mission.levels as lvl, i}
            {#if !lvl.completed}
              <i class="ml-1 fas fa-times has-text-grey-light" />
            {:else}
              <i
                class="ml-1 fas fa-check has-text-success"
                in:spin={{ duration: 1000, delay: 300 }}
              />
            {/if}
          {/each}
        </div>
      </div>
    {/each}
  {/if}

  <div>
    <span class="tag is-dark is-size-5">AVAILABLE MISSIONS</span>
  </div>
  {#if availableMissions.length}
    {#each availableMissions as mission, i}
      <div
        class="card border-{mission.difficulty == 3
          ? 'danger'
          : mission.difficulty == 2
          ? 'warning'
          : 'success'}"
        in:fly={{ x: -100, duration: 1000, delay: 500 * (i + 2) }}
        out:fly={{ x: 100, duration: 500 }}
        on:click={() => startThemission(mission.id)}
      >
        <p class="is-inline-block is-size-5">
          {mission.title}
        </p>
        <div class="is-inline-block">
          <span class="tag is-light baseline">
            <small class="mr-1">Difficulté</small>
            {#each Array(mission.difficulty).fill(undefined) as x, i}
              <i class="ml-1 fas fa-times has-text-danger" />
            {/each}
          </span>
        </div>
      </div>
    {/each}
  {:else}
    <div class="has-text-grey-light">No available missions</div>
  {/if}
</div>

{#if modalToggle}
  <ModalWithCharacter
    {message}
    {character}
    on:close={closeModal}
    on:okClicked={closeModal}
  />
{/if}

{#if responseModalToggle}
  <ModalResponseWithCharacter
    {message}
    {character}
    on:close={justCloseResponseModal}
    on:okClicked={closeResponseModal}
  />
{/if}

{#if winModal}
  <ModalWin message={winText} character={winCharacter} />
{/if}

<style>
  div {
    margin: 0.5em 0;
  }

  .card {
    width: 80%;
    padding: 0.25em 1em;
    border-radius: 0.5em;
    border: solid 2px;
  }

  .border-danger {
    border-color: #f14668 !important;
  }
  .border-warning {
    border-color: #ffe08a !important;
  }
  .border-success {
    border-color: #48c78e !important;
  }

  .tag.baseline {
    align-items: baseline;
  }
</style>
