<script>
  import { elasticOut } from "svelte/easing";
  import { fade, fly } from "svelte/transition";
  //import { createEventDispatcher } from "svelte";

  export let message;
  export let character = 1;

  //const dispatch = createEventDispatcher();

  const charactersList = [
    "./images/Tigre1.jpeg",
    "./images/Tigre2.jpeg",
    "./images/Tigre3.jpeg",
  ];

  /* const okClicked = () => {
    dispatch("okClicked", {});
  };

  const close = () => {
    //modalToggle = false;
    dispatch("close", {});
  }; */

  function whoosh(node, params) {
    const existingTransform = getComputedStyle(node).transform.replace(
      "none",
      ""
    );

    return {
      delay: params.delay || 0,
      duration: params.duration || 400,
      easing: params.easing || elasticOut,
      css: (t, u) => `transform: ${existingTransform} scale(${t})`,
    };
  }
</script>

<div class="modal is-active" out:fade>
  <div class="modal-background" />
  <div class="modal-content" in:whoosh={{ duration: 2000, delay: 0 }}>
    <img src={charactersList[character]} alt="character" />
    <div class="card has-text-centered win-text">
      <h1 class="is-size-1 has-text-warning">{message}</h1>
    </div>
  </div>
</div>

<style>
  .modal-background {
    /*  background-color: transparent; */
    left: -200vw;
  }
  .modal-content {
    width: 100%;
    overflow: hidden;
  }
  .card {
    padding: 1em;
    border-radius: 0.5rem;
  }

  img {
    margin-bottom: -4em;
    width: 100%;
  }
  .win-text {
    width: 100%;
  }
  .win-text h1 {
    font-weight: 900;
  }
</style>
