<script>
  import { onMount } from "svelte";
  import { fade, fly, slide } from "svelte/transition";
  import { playClicked } from "../game";

  let ready;

  const startGame = () => {
    $playClicked = 1;
  };
  onMount(() => {
    ready = true;
  });
</script>

{#if ready}
  <button
    class="button is-danger is-rounded main-font-family"
    in:fade={{ duration: 1500 }}
    out:fade={{ duration: 1000 }}
    on:click={startGame}
  >
    Jouer
  </button>
{/if}

<style>
  .button {
    font-size: 2em;
    font-weight: 700;
    background: linear-gradient(#ec6565, #bb0000);
  }
</style>
