<script>
  import { onMount } from "svelte";
  import { fade, fly, slide } from "svelte/transition";
  import ModalWithCharacter from "./modals/ModalWithCharacter.svelte";

  let ready, modalToggle;

  let message = "Désolé pas encore prèt... \n Inchallah très bientôt!";

  const openModal = () => {
    modalToggle = true;
  };
  const closeModal = () => {
    modalToggle = false;
  };

  onMount(() => {
    ready = true;
  });
</script>

{#if ready}
  <button
    class="button is-info is-outlined is-rounded main-font-family"
    in:fade={{ duration: 2000, delay: 600 }}
    out:fade={{ duration: 500 }}
    on:click={openModal}
  >
    T3allem ella3b
  </button>
{/if}

{#if modalToggle}
  <ModalWithCharacter
    {message}
    character="2"
    on:close={closeModal}
    on:okClicked={closeModal}
  />
{/if}

<style>
  .button.is-info.is-outlined {
    background-color: transparent;
    border-color: #ffff;
    color: #ffff;
  }
</style>
