<script>
  import { onMount } from "svelte";
  import { fade, fly } from "svelte/transition";
  import { elasticOut } from "svelte/easing";
  import { score } from "../game";

  let ready;

  function spin(node, { duration, delay }) {
    return {
      duration,
      delay,
      css: (t) => {
        const eased = elasticOut(t);

        return `
					transform: scale(${eased}) rotate(${eased * 1080}deg);
          `;
      },
    };
  }

  onMount(() => {
    ready = true;
  });
</script>

{#if ready}
  <div
    class="ui is-size-3"
    in:fly={{ y: -100, duration: 1500, delay: 200 }}
    out:fly={{ y: 100, duration: 1000 }}
  >
    <div class="left">
      <i class="fas fa-lg fa-user-circle m-t25" />
      <div class="player-display ml-2">
        <div class="player-name">
          <span>SKON(Moh)</span>
        </div>
        <div
          class="player-score m-t-25 is-size-5 has-text-warning"
          in:spin={{ duration: 1500, delay: 1000 }}
        >
          <i class="fas fa-star" />
          <span>{$score}</span>
        </div>
      </div>
    </div>
    <div class="right">
      <i class="fas fa-ellipsis-v" />
    </div>
  </div>
{/if}

<style>
  .ui {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .left {
    display: flex;
  }
  .m-t25 {
    margin-top: 0.25em;
  }
  .m-t-25 {
    margin-top: -0.25em;
  }
</style>
