<script>
  import { createEventDispatcher } from "svelte";
  import { fade, fly } from "svelte/transition";

  /* export let modalToggle; */
  export let message;
  export let character = 0;

  let text = message.split("\n");

  const dispatch = createEventDispatcher();

  const charactersList = [
    "./images/Tigre1.jpeg",
    "./images/Tigre2.jpeg",
    "./images/Tigre3.jpeg",
  ];

  const okClicked = () => {
    dispatch("okClicked", {});
  };

  const close = () => {
    //modalToggle = false;
    dispatch("close", {});
  };
</script>

<div class="modal is-active" in:fly={{ x: 50, duration: 500 }} out:fade>
  <div
    class="modal-background"
    in:fade={{ delay: 100 }}
    out:fade
    on:click={close}
  />
  <div class="modal-content" class:has-text-right={character == 2}>
    <img src={charactersList[character]} alt="character" />
    <div class="card has-text-left is-size-4">
      {#each text as p}
        <p>{p}</p>
      {/each}
      <div class="has-text-right is-size-4 ">
        <div on:click={okClicked} class="is-30-percent is-clickable">ok...</div>
      </div>
    </div>
  </div>
  <button class="modal-close is-large" aria-label="close" />
</div>

<style>
  .modal-background {
    /*  background-color: transparent; */
    left: -200vw;
  }
  .modal-content {
    width: 90%;
  }
  .card {
    padding: 1em;
    border-radius: 0.5rem;
  }

  img {
    width: 5em;
    margin-bottom: -1em;
    border-radius: 50%;
  }

  .is-30-percent {
    display: inline-block;
    width: 30%;
    padding: 0 1em;
  }
</style>
